<template>
  <i
    class="iconfont icon-container"
    :class="fontClass"
    :style="{
      fontSize: size + 'px',
    }"
  ></i>
</template>

<script>
export const classMap = {
  account: "icon-account",
  wishlist: "icon-wishlist",
  cart: "icon-cart",
  checkout: "icon-checkoutline",
  login: "icon-login",
  search: "icon-search",
  categories: "icon-fenlei",
  clothing: "icon-baobao",
  electronics: "icon-electronics",
  shoes: "icon-shoes",
  watches: "icon-clock",
  jewellery: "icon-zhubaoshipin",
  health: "icon-jiankang",
  kids: "icon-send",
  sports: "icon-sports",
  garden: "icon-jiankang1",
  left: "icon-zuojiantou",
  right: "icon-youjiantou",
  basket: "icon-Basket",
  grid: "icon-grid",
  list: "icon-list",
  down: "icon-xiangxia",
  chat: "icon-chat-dot-round",
};
export const types = Object.keys(classMap);

export default {
  props: {
    type: {
      type: String,
      required: true,
    },
    size: {
      type: Number,
      defult: 16,
    },
  },
  computed: {
    // 图标类样式
    fontClass() {
      return classMap[this.type];
    },
  },
};
</script>
<style>
@import "//at.alicdn.com/t/c/font_3771505_lbk6h9vpom.css";
.iconfont {
  color: inherit;
  font-size: inherit;
}
</style>
