import Vuex from "vuex";
import Vue from "vue";
Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    messageList: [
      {
        username: "小明",
        text: "你好",
        createDate: "2022-12-01 12:00:01",
      },
    ],
    cart: [],
    totalPrice: 0,
    token: localStorage.getItem("token"),
  },
  mutations: {
    addMessage(state, payload) {
      console.log("=== store Message:", payload);
      state.messageList.push(payload);
    },

    updateUsername(state, payload) {
      console.log("=== store updateUsername:", payload);
      state.messageList[0].username = payload.username;
    },
    updateCart(state, payload) {
      console.log("=== store cart:", payload);
      state.cart = payload;
    },
    updateTotalPrice(state, payload) {
      console.log("=== store totalPrice:", payload);
      state.totalPrice = payload;
    },
    updateToken(state, payload) {
      console.log("=== store updateToken:", payload);
      state.token = payload;
    },
  },
});
export default store;
